* {
  font-family: Roboto, sans-serif;
  padding: 0;
  margin: 0;
}

html, body {
  width: 100%;
  height: 100%;
}

.flexbox {
  width: 58%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
}

.search {
  margin: 20px;
}

.search > h3 {
  font-weight: normal;
}

.search > h1,
.search > h3 {
  color: white;
  margin-bottom: 15px;
  text-shadow: 0 1px #0091c2;
}

.search > div {
  display: inline-block;
  position: relative;
  -webkit-filter: drop-shadow(0 1px #0091c2);
          filter: drop-shadow(0 1px #0091c2);
}

.search > div:after {
  content: "";
  background: white;
  width: 4px;
  height: 20px;
  position: absolute;
  top: 21px;
  right: -5px;
  transform: rotate(135deg);
}

.search > div > input {
  color: white;
  font-size: 16px;
  background: transparent;
  width: 25px;
  height: 25px;
  padding: 10px;
  border: solid 3px white;
  outline: none;
  border-radius: 35px;
  transition: width 0.5s;
}

.search > div > input::-webkit-input-placeholder {
  color: #efefef;
  opacity: 0;
  -webkit-transition: opacity 150ms ease-out;
  transition: opacity 150ms ease-out;
}

.search > div > input:-ms-input-placeholder {
  color: #efefef;
  opacity: 0;
  -ms-transition: opacity 150ms ease-out;
  transition: opacity 150ms ease-out;
}

.search > div > input::-ms-input-placeholder {
  color: #efefef;
  opacity: 0;
  -ms-transition: opacity 150ms ease-out;
  transition: opacity 150ms ease-out;
}

.search > div > input::placeholder {
  color: #efefef;
  opacity: 0;
  transition: opacity 150ms ease-out;
}

.search > div > input:focus::-webkit-input-placeholder {
  opacity: 1;
}

.search > div > input:focus:-ms-input-placeholder {
  opacity: 1;
}

.search > div > input:focus::-ms-input-placeholder {
  opacity: 1;
}

.search > div > input:focus::placeholder {
  opacity: 1;
}

.search > div > input:not(:-ms-input-placeholder) {
  width: 250px;
  height: 40px;
}

.search > div > input:focus,
.search > div > input:not(:placeholder-shown) {
  width: 250px;
  height: 40px;
}
